<template>
  <div class="business">
    <mt-header title="理财分享业务平台">
    </mt-header>
    <span class="header-btn right" style="background: url('./static/img/header/setting.png')"></span>
    <div class="inform-overview" style="background: url('./static/img/business/bg.png')">
      <div class="avatar-border" style="background: url(./static/img/business/avatar_border.png)">
        <div class="avatar" :style="'background: url('+ datas.avatar +')'"></div>
      </div>
      <div class="name">
        {{datas.name}}</div>
      <div class="job">
        <span class="company">{{datas.company}}</span><span class="jobtitle">{{datas.jobtitle}}</span>
      </div>
      <div class="applyfor">
        <p><span class="num">51</span>项</p>
        <p>申请</p>
      </div>
      <div class="gap"></div>
      <div class="consult">
        <p><span class="num">5</span>人</p>
        <p>咨询</p>
      </div>
    </div>
    <div class="achievement" style="background: url(./static/img/business/achievement_btn.png)">
      <div class="icon" style="background: url(./static/img/business/achievement_cup.png)"></div>
      <span>业绩</span>
    </div>
    <div class="todo">
      <div class="title">待办事项
        <mt-badge size="small" color="#00b25b">30</mt-badge>
      </div>
      <div class="more">更多</div>
      <ul class="list">
        <li v-for="item in datas.todo" class="item">
          <span class="type" :class="item.type"></span><span class="title">{{item.title}}</span><span class="time">{{item.time}}</span>
        </li>
      </ul>
    </div>
    <div class="bot-gap"></div>
  </div>
</template>
<script>
export default {
  data: function() {
    return {

    }
  },
  props: {
    datas: Object
  },
  components: {},
  mounted: function() {
    console.log(this.datas.avatar)
  },
  methods: {}
}
</script>
<style scoped>
.business {
  background-color: #fff;
}

.inform-overview {
  position: relative;
  width: 100%;
  height: 4.08rem;
  background-repeat: no-repeat!important;
  background-size: 100% 100%!important;
  color: #fff;
}

.avatar-border {
  position: relative;
  top: 0.12rem;
  left: 50%;
  width: 1.34rem;
  height: 1.34rem;
  margin-left: -0.67rem;
  background-repeat: no-repeat!important;
  background-size: 100% 100%!important;
}

.avatar {
  position: relative;
  top: 0.04rem;
  left: 50%;
  width: 1.26rem;
  height: 1.26rem;
  margin-left: -0.63rem;
  background-repeat: no-repeat!important;
  background-size: contain!important;
}

.name {
  position: relative;
  top: 0.14rem;
  width: 100%;
  text-align: center;
  font-size: 0.3rem;
}

.job {
  position: relative;
  top: 0.2rem;
  width: 100%;
  text-align: center;
  color: #AFDECA;
  font-size: 0.24rem;
}

.job>* {
  display: inline-block;
  padding: 0.05rem 0.08rem;
  background-color: #00aa58;
}

.job>.company {
  padding-right: 0;
  border-top-left-radius: 0.2rem;
  border-bottom-left-radius: 0.2rem;
}

.job>.jobtitle {
  padding-left: 0;
  border-top-right-radius: 0.2rem;
  border-bottom-right-radius: 0.2rem;
}

.job>.jobtitle:before {
  content: '|';
}

.applyfor,
.consult {
  position: absolute;
  top: 2.3rem;
  font-size: 0.22rem;
  text-align: center;
}

.applyfor .num,
.consult .num {
  font-size: 0.7rem;
}

.applyfor p:nth-child(2),
.consult p:nth-child(2) {
  font-size: 0.26rem;
}

.applyfor {
  left: 1.16rem;
}

.consult {
  right: 1.16rem;
}

.gap {
  position: absolute;
  left: 50%;
  margin-left: -0.005rem;
  top: 2.5rem;
  width: 0.01rem;
  height: 0.8rem;
  background-color: rgba(255, 255, 255, 0.6);
}

.achievement {
  position: relative;
  top: -0.15rem;
  left: 50%;
  margin-left: -1.3rem;
  width: 2.5rem;
  height: 0.7rem;
  background-repeat: no-repeat!important;
  background-size: contain!important;
  font-size: 0.34rem;
  color: #f06b2f;
  text-align: center;
  line-height: 0.7rem;
}

.achievement span {
  padding-left: 0.4rem;
}

.achievement .icon {
  position: absolute;
  top: 0.18rem;
  left: 0.6rem;
  width: 0.33rem;
  height: 0.33rem;
  background-repeat: no-repeat!important;
  background-size: contain!important;
}

.todo {
  position: relative;
  padding: 0.1rem 0.2rem 1.8rem;
}

.todo>.title {
  font-size: 0.3rem;
  border-left: 0.04rem solid #ed973b;
  padding-left: 0.1rem;
  position: relative;
  width: 1.5rem;
}

.todo>.title .mint-badge {
  position: absolute;
  padding: 0;
  width: 0.3rem;
  height: 0.24rem;
  border-radius: 0.12rem;
  font-size: 0.12rem;
  line-height: 0.24rem;
  text-align: center;
}

.todo .more {
  position: absolute;
  top: 0.1rem;
  right: 0.2rem;
  font-size: 0.24rem;
  color: #646464;
}

.todo .item {
  position: relative;
  width: 100%;
  height: 0.66rem;
}

.todo .item>* {
  display: inline-block;
  margin-bottom: 0.3rem;
  vertical-align: middle;
}

.todo .item .type {
  margin-left: 0.24rem;
  margin-right: 0.3rem;
  width: 0.46rem;
  height: 0.24rem;
  border-radius: 0.05rem;
  background-color: #999;
  font-size: 0.16rem;
  text-align: center;
  line-height: 0.24rem;
  color: #fff;
}

.todo .item .type:after {
  content: '未知'
}

.todo .item .type.collection {
  background-color: #FC6F51;
}

.todo .item .type.collection:after {
  content: '催收'
}

.todo .item .type.declaration {
  background-color: #47D0AD;
}

.todo .item .type.declaration:after {
  content: '公告'
}

.todo .item .title {
  width: calc(100% - 2.8rem);
  font-size: 0.26em;
  color: #646464;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.todo .item .time {
  position: absolute;
  top: 0.4rem;
  right: 0;
  width: 1.8rem;
  padding-left: 0.3rem;
  font-size: 0.18em;
  color: #646464;
  text-align: right;
}

.bot-gap {
  position: fixed;
  bottom: 0.96rem;
  width: 100%;
  height: 0.2rem;
  border-top: 1px solid #dcdcdc;
  background-color: #EBEBEB;
}
</style>



