import Vue from 'vue'
import VueLocalStorage from 'vue-localstorage'
import MintUI from 'mint-ui'
import 'mint-ui/lib/style.css'
import 'animate.css/animate.css'
import App from './App'

Vue.use(VueLocalStorage)
Vue.use(MintUI)

/* eslint-disable no-new */

new Vue({
  el: '#app',
  template: '<App/>',
  components: { App },
  localStorage: {
    localList: {
      type: Array,
      default: [{
        label: '默认项目',
        done: true
      }]
    }
  }
})



