<template>
  <div id="app">
    <div class="page-wrap">
      <mt-tab-container class="page-tabbar-container" v-model="selected">
        <mt-tab-container-item id="首页">
          <business :datas="business_datas"></business>
        </mt-tab-container-item>
        <mt-tab-container-item id="营销产品">
          <marketing></marketing>
        </mt-tab-container-item>
        <mt-tab-container-item id="我的客户">
          <mt-header :title="header_word">
          </mt-header>
          <mt-cell v-for="n in 7" :title="'我的客户 ' + n" />
        </mt-tab-container-item>
        <mt-tab-container-item id="产品资讯">
          <mt-header :title="header_word">
          </mt-header>
          <div class="page-part">
            <mt-cell v-for="n in 12" :title="'产品资讯 ' + n" />
          </div>
          <mt-button type="danger" size="large">退出</mt-button>
        </mt-tab-container-item>
      </mt-tab-container>
    </div>
    <mt-tabbar class="bot" v-model="selected">
      <mt-tab-item id="首页">
        <img slot="icon" :src="business_img"> 首页
      </mt-tab-item>
      <mt-tab-item id="营销产品">
        <img slot="icon" :src="marketing_img"> 营销产品
      </mt-tab-item>
      <mt-tab-item id="我的客户">
        <img slot="icon" :src="my_customers_img"> 我的客户
      </mt-tab-item>
      <mt-tab-item id="产品资讯">
        <img slot="icon" :src="inform_img"> 产品资讯
      </mt-tab-item>
    </mt-tabbar>
  </div>
</template>
<script>
import business from './Business/index'
import marketing from './Marketing/index'

export default {
  data: function() {
    return {
      business_datas: {
        test_word: 'forMainApp',
        name: '王俊凯',
        avatar: './static/img/business/avatar.png',
        company: '招商银行',
        jobtitle: '高级理财师',
        todo: [{
            type: 'collection',
            title: '张伟伟还款提醒',
            time: '2016-06-18 19:23'
          }, {
            type: 'declaration',
            title: '张伟伟张伟伟张伟伟张伟伟还款提醒',
            time: '2016-06-18 19:23'
          }, {
            type: 'collection',
            title: '张伟伟还款提醒',
            time: '2016-06-18 19:23'
          }, {
            type: 'declaration',
            title: '张伟伟张伟伟张伟伟张伟伟还款提醒',
            time: '2016-06-18 19:23'
          }, {
            type: 'collection',
            title: '张伟伟还款提醒',
            time: '2016-06-18 19:23'
          }, {
            type: 'declaration',
            title: '张伟伟张伟伟张伟伟张伟伟还款提醒',
            time: '2016-06-18 19:23'
          }, {
            type: 'collection',
            title: '张伟伟还款提醒',
            time: '2016-06-18 19:23'
          }, {
            type: 'declaration',
            title: '张伟伟张伟伟张伟伟张伟伟还款提醒',
            time: '2016-06-18 19:23'
          }

        ]
      },
      business_img: './static/img/tab_business_active.png',
      marketing_img: './static/img/tab_marketing.png',
      my_customers_img: './static/img/tab_my_customers.png',
      inform_img: './static/img/tab_inform.png',
      marketing_active: '',
      business_active: '',
      business_active: '',
      selected: '首页',
      header_word: '理财分享业务平台'
    }
  },
  components: {
    business,
    marketing
  },
  watch: {
    selected: function(newItems) {
      switch (newItems) {
        case '首页':
          this.changeTab('_active', '', '', '')
          this.header_word = '理财分享业务平台'
          break
        case '营销产品':
          this.changeTab('', '_active', '', '')
          this.header_word = '营销活动'
          break
        case '我的客户':
          this.changeTab('', '', '_active', '')
          this.header_word = '我的客户'
          break
        case '产品资讯':
          this.changeTab('', '', '', '_active')
          this.header_word = '产品资讯'
          break
      }
    }
  },
  mounted: function() {
    (function() {
      document.addEventListener('DOMContentLoaded', function() {
        var html = document.documentElement;
        var windowWidth = html.clientWidth;
        html.style.fontSize = windowWidth / 6.4 + 'px';
        // 设计宽度为640px,以此为基准
        // 等价于html.style.fontSize = windowWidth / 640 * 100 + 'px';
      }, false);
    })();

  },
  methods: {
    changeTab: function(business, marketing, my_customers, inform) {
      this.business_img = './static/img/tab_business' + business + '.png'
      this.marketing_img = './static/img/tab_marketing' + marketing + '.png'
      this.my_customers_img = './static/img/tab_my_customers' + my_customers + '.png'
      this.inform_img = './static/img/tab_inform' + inform + '.png'
    }
  }
}
</script>
<style>
@import './Font-Awesome/css/font-awesome.min.css';
* {
  padding: 0;
  margin: 0;
}

.clean {
  height: 0;
  font-size: 0;
  clear: both;
}

.mint-tab-container {
  overflow: scroll;
  height: 100vh;
  position: relative;
  background-color: #EBEBEB;
}

.mint-header {
  position: relative;
  display: block;
  font-size: 0.34rem;
  height: 1.1rem;
  color: #fff;
  text-align: center;
  background-color: #00AF57;
}

.mint-header-title {
  height: 0.75rem;
  padding-top: 0.35rem;
}

.header-btn {
  height: 0.5rem;
  width: 0.5rem;
  top: 0.3rem;
  position: absolute;
  background-position: center!important;
  background-size: contain!important;
  background-repeat: no-repeat!important;
}

.header-btn.left {
  left: 0.24rem;
  text-align: left;
}

.header-btn.right {
  right: 0.24rem;
  text-align: right;
}

.mint-tabbar > .mint-tab-item {
  height: 0.96rem;
  color: #999;
  background-color: #fff;
  border-top: 1px solid #dcdcdc;
}

.mint-tabbar > .mint-tab-item.is-selected {
  background-color: #fff;
  color: #00b55c;
}

.mint-tab-item-icon {
  width: 0.36rem;
  height: 0.36rem;
  margin: 0.13rem auto 0;
}

.mint-tab-item-label {
  font-size: 0.22rem;
  padding-top: 0.14rem;
}
</style>



