<template>
  <div class="marketing">
    <mt-header title="营销产品">
    </mt-header>
    <div class="nav">
      <mt-button size="small" v-for="(tab_item, tab_index) in datas" :id="'tab_' + tab_index" :data-index="tab_index" @click="clickTab">{{tab_item.tab_name}}</mt-button>
    </div>
    <div class="page-tab-container">
      <mt-tab-container class="page-tabbar-tab-container" v-model="active" swipeable>
        <mt-tab-container-item v-for="(container_item, container_index) in datas" class="tab_container" :id="'container_'+container_index">
          <productList :datas="container_item.list"></productList>
        </mt-tab-container-item>
      </mt-tab-container>
    </div>
  </div>
</template>
<script>
import productList from './product_list'
export default {
  data: function() {
    return {
      active: 'container_0',
      datas: [{
        tab_name: '全部',
        list: [{
          title: '全部-长盛电子信息主题灵活配置混合型证券投资基金'
        }, {
          title: '全部-工银金融地产混合'
        }]
      }, {
        tab_name: '出门旅行',
        list: [{
          title: '出门旅行-长盛电子信息主题灵活配置混合型证券投资基金'
        }, {
          title: '出门旅行-工银金融地产混合'
        }]
      }, {
        tab_name: '教育',
        list: [{
          title: '教育-长盛电子信息主题灵活配置混合型证券投资基金'
        }, {
          title: '教育-工银金融地产混合'
        }]
      }, {
        tab_name: '健康',
        list: [{
          title: '健康-长盛电子信息主题灵活配置混合型证券投资基金'
        }, {
          title: '健康-工银金融地产混合'
        }]
      }]

    }
  },
  components: {
    productList
  },
  methods: {
    clickTab: function(e) {
      console.log(e.target.dataset.index)
      this.active = 'container_' + e.target.dataset.index
    }
  }
}
</script>
<style scoped>
.item {
  display: inline-block;
}

.nav {
  padding: 10px;
}

.link {
  color: inherit;
  padding: 20px;
  display: block;
}
</style>



